import React, { useState, useEffect } from 'react';
import { useAuth } from '../../common/context/AuthContext';
import { supabase } from "../../common/services/supabaseClient";
import { UnifiedBackground, UnifiedCard, SharedStyles } from "../../common/components/SharedTheme";
import { Gift, Clock, Copy, Tag } from 'lucide-react';

function CustomerCoupons() {
  const { user } = useAuth();
  const [copiedCode, setCopiedCode] = useState(null);
  const [coupons, setCoupons] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Load coupons from Supabase
  useEffect(() => {
    loadCoupons();
  }, []);

  const loadCoupons = async () => {
    try {
      setLoading(true);
      // For now, use a simple static set of coupons
      // In a real implementation, this would come from Supabase
      const staticCoupons = [
        {
          id: 1,
          code: 'WELCOME20',
          title: '20% Off First Purchase',
          description: 'Get 20% discount on your first investment with BrightPlanet Ventures',
          discount: '20%',
          minAmount: 1000,
          expiryDate: '2024-12-31',
          category: 'Welcome Offer',
          isActive: true
        },
        {
          id: 2,
          code: 'REFER25',
          title: '25% Referral Bonus',
          description: 'Get 25% bonus when you refer a friend',
          discount: '25%',
          minAmount: 500,
          expiryDate: '2024-12-31',
          category: 'Referral',
          isActive: true
        },
        {
          id: 3,
          code: 'SAVE500',
          title: '₹500 Off on ₹3000+',
          description: 'Save ₹500 on savings above ₹3000',
          discount: '₹500',
          minAmount: 3000,
          expiryDate: '2024-11-30',
          category: 'Special Offer',
          isActive: true
        }
      ];

      setCoupons(staticCoupons);
      setError(null);
    } catch (err) {
      setError('Failed to load coupons');
    } finally {
      setLoading(false);
    }
  };



  const handleCopyCode = (code) => {
    navigator.clipboard.writeText(code);
    setCopiedCode(code);
    setTimeout(() => setCopiedCode(null), 2000);
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-IN', {
      day: 'numeric',
      month: 'short',
      year: 'numeric'
    });
  };

  if (loading) {
    return (
      <>
        <SharedStyles />
        <UnifiedBackground>
          <div className="min-h-screen pt-40 px-8 pb-8">
            <div className="max-w-7xl mx-auto">
              <div className="flex justify-center items-center">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-400"></div>
                <p className="text-white ml-4">Loading coupons...</p>
              </div>
            </div>
          </div>
        </UnifiedBackground>
      </>
    );
  }

  if (error) {
    return (
      <>
        <SharedStyles />
        <UnifiedBackground>
          <div className="min-h-screen pt-40 px-8 pb-8">
            <div className="max-w-7xl mx-auto">
              <div className="text-center">
                <p className="text-red-400 text-lg">{error}</p>
                <button
                  onClick={loadCoupons}
                  className="mt-4 bg-orange-600 hover:bg-orange-700 text-white px-6 py-2 rounded-lg transition-colors duration-200"
                >
                  Retry
                </button>
              </div>
            </div>
          </div>
        </UnifiedBackground>
      </>
    );
  }

  return (
    <>
      <SharedStyles />
      <UnifiedBackground>
        <div className="min-h-screen pt-24 px-8 pb-8">
          <div className="max-w-7xl mx-auto">
            {/* Header */}
            <div className="mb-4">
              <h1 className="text-3xl font-bold text-white flex items-center gap-3 mb-2">
                <Gift className="text-yellow-400" size={32} />
                Available Coupons
              </h1>
              <p className="text-blue-200 text-lg">
                Save more on your savings with exclusive offers
              </p>
            </div>

        {/* Coupons Table */}
          <UnifiedCard>
              <div className="overflow-x-auto">
                <table className="w-full">
                  <thead className="bg-gray-700">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Code</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Title</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Discount</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Category</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Actions</th>
                  </tr>
                </thead>
                  <tbody className="divide-y divide-gray-700">
                    {coupons.length === 0 ? (
                      <tr>
                        <td colSpan="5" className="px-6 py-12 text-center text-gray-400">
                          No coupons available
                        </td>
                      </tr>
                    ) : (
                      coupons.map((coupon) => (
                        <tr key={coupon.id} className="hover:bg-gray-700">
                          <td className="px-6 py-4 whitespace-nowrap">
                            <div className="bg-slate-700/50 px-3 py-2 rounded-lg border border-slate-600/50">
                              <span className="text-white font-mono font-bold text-sm">{coupon.code}</span>
                            </div>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <span className="text-white font-medium">{coupon.title}</span>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <span className="inline-flex items-center px-2.5 py-1 rounded-full text-xs font-medium transition-all bg-orange-500/20 text-orange-300 border-orange-400/60">
                              {coupon.discount}
                            </span>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <span className="inline-flex items-center px-2.5 py-1 rounded-full text-xs font-medium transition-all bg-blue-500/20 text-blue-300 border-blue-400/60">
                              {coupon.category}
                            </span>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <button
                              onClick={() => handleCopyCode(coupon.code)}
                              className="flex items-center gap-2 px-3 py-1 bg-blue-600 hover:bg-blue-700 text-white text-xs font-medium rounded-md transition-colors duration-200"
                            >
                              <Copy className="w-3 h-3" />
                              Copy
                            </button>
                          </td>
                        </tr>
                      ))
                    )}
                  </tbody>
                </table>
              </div>
            </UnifiedCard>

          </div>
        </div>
      </UnifiedBackground>
    </>
  );
  }

export default CustomerCoupons;
